import { Component,  OnInit,  ChangeDetectionStrategy,  ViewChild,  TemplateRef} from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms'
import { AppService } from '../app.component.service';
import { ToastrService } from 'ngx-toastr';
import {  startOfDay, endOfDay, subDays, addDays, endOfMonth, isSameDay, isSameMonth,addHours} from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {  CalendarEvent,  CalendarEventAction, CalendarEventTimesChangedEvent,  CalendarView} from 'angular-calendar';
const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  selector: 'app-schedule',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.css']
})
export class ScheduleComponent implements OnInit {
  dobSettings = {
    bigBanner: false,
    timePicker: true,
    format: 'dd-MM-yyyy hh:mm',
    defaultOpen: true,
    closeOnSelect: true,
    rangepicker: false

}
  jobId;
  candidateID;
  userId;
  jobTitle;
  hideReason = true;
  CandiName="";
  min = new Date();
  editMode = false;
  conformButton =false;
  customerDetails;
  eventArray= [] ;
 @ViewChild('modalContent', {static: true})
  modalContent: TemplateRef<any>;
  view: CalendarView = CalendarView.Month;
  CalendarView = CalendarView;
  viewDate: Date = new Date();
      date: Date = new Date();
    settings = {
        bigBanner: true,
        timePicker: false,
        format: 'dd-MM-yyyy',
        defaultOpen: true
    }
  modalData: {
    action: string;
    event: CalendarEvent;
  };
  actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-pencil"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.handleEvent('Edited', event);
      }
    },
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.events = this.events.filter(iEvent => iEvent !== event);
        this.handleEvent('Deleted', event);
      }
    }
  ];
  refresh: Subject<any> = new Subject();
  events: CalendarEvent[] = [];
  modes = [{"value":"Skype"},{"value":"Face To Face"},{"value":"Telephonic"}]
  userDetailsForm : FormGroup;
  submitted = false;
  rescheduleForm = false
  activeDayIsOpen: boolean = true;
  constructor(private modal: NgbModal,
    private route: ActivatedRoute,
    private appService:AppService,
    private toastr: ToastrService,
    private router: Router) {
      this.userId = localStorage.getItem('loginSessId');
  }
ngOnInit() {
      this.initForm();
      this.route.params
      .subscribe(
        (params: Params) => {
          this.jobId = params.jobId;
          this.candidateID = params.id;
          console.log(this.candidateID);
          this.editMode = params.jobId != null;

            });
      if(this.jobId === undefined && this.candidateID !== undefined){
            this.rescheduleForm = true ;
            this.editMode = true;
            console.log(this.rescheduleForm);
          }
      console.log(this.jobId,this.candidateID)
      if(this.editMode && !this.rescheduleForm ){
          this.getEventsUser(this.userId);
          //this.initForm();
          this.getDetails();
        }
        if(this.rescheduleForm){
          this.getDetailsReschedule()
        }
      if(!this.editMode){
        console.log('normal');
        this.getEventsUser(this.userId);
      }
     }
dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      this.viewDate = date;
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
    }
  }

reschedule(){
  let candidateRescheduled = this.eventArray.find( data => data.interview_date === this.jobTitle )
  this.setFormRescheduledUser(candidateRescheduled);
  }
  setFormRescheduledUser(data){
    console.log(data);
    this.editMode = true;
    this.candidateID = data._id;
    this.rescheduleForm = true;
    this.userDetailsForm.setValue({
      title:data.job_id.job_title,
      name:data.candidate_name,
      location:data.location,
      mode : data.interview_type,
      date :data.interview_date,
      reason: "",
      period: ""
    })
    this.hideReason = false ;
  }
eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }

handleEvent(action: string, event: CalendarEvent): void {
    //this.modalData = { event, action };
    console.log(action, event)
    this.CandiName = event.title ;
    this.jobTitle = event.start;
  //  this.modal.open(this.modalContent, { size: 'lg' });
  }
addEvent(): void {
    this.events.push({
      title: 'New event',
      start: startOfDay(new Date()),
      end: endOfDay(new Date()),
      color: colors.red,
      draggable: true,
      resizable: {
        beforeStart: true,
        afterEnd: true
      }
    });
    this.refresh.next();
  }
  getDetailsReschedule(){
    this.appService.getRescheduleRequestDetails(this.candidateID).subscribe((data)=>{
      console.log(data);
      this.customerDetails = data.result;
      this.setReschduleForm();
    },(err)=>{
      console.log(err);
    })
  }
getDetails(){
  console.log(JSON.parse(this.userId),this.jobId,this.candidateID);
  this.appService.getProfileData(JSON.parse(this.userId),this.jobId,this.candidateID).subscribe((data)=>{
    this.customerDetails = data.result[0];
    console.log(this.customerDetails);
    this.setUserForm();
  },(error)=>{
    console.log(error);
  })
}
initForm(){
 console.log(this.min)
this.userDetailsForm = new FormGroup({
      title: new FormControl('', Validators.required),
      name: new FormControl('', Validators.required),
      mode: new FormControl('', Validators.required),
      location: new FormControl('', Validators.required),
      date: new FormControl(new Date(),Validators.required),
      period: new FormControl(''),
      reason: new FormControl(''),
      //date: new FormControl('',Validators.required),

      //time:new FormControl('',Validators.required),
       })
  }
  onDateSelect(date: any) {
    console.log(date);
}
onSubmit(){
    this.submitted = true;
    if(this.userDetailsForm.valid){
  console.log(this.userDetailsForm.valid)
  var control = this.userDetailsForm.controls;
  var form = {
  "job_id":this.jobId,
  "client_id":this.customerDetails.client_id,
  "candidate_id":this.candidateID.trim(),
  "location":control.location.value,
  "interview_type":control.mode.value,
  "interview_date":control.date.value,
  "interview_time":control.date.value,
  "candidate_name":control.name.value
  }
  console.log(form);

  this.appService.setInterviewOfSingleCandidate(form).subscribe((data)=>{
    console.log(data);
    this.userDetailsForm.reset;
    this.editMode = false ;
    this.toastr.success('Schedule Succesfully')
    this.getDetails();
    this.router.navigate(['/clientHeader/schedule']);

     },(error)=>{
      console.log(error)
    })
  }
  }
  setReschduleForm(){
    if(this.rescheduleForm){
      this.userDetailsForm.setValue({
       title:this.customerDetails.job_id.job_title,
        name:this.customerDetails.candidate_name,
        location:this.customerDetails.location,
        mode : this.customerDetails.interview_type,
        date :this.customerDetails.interview_date,
        reason: this.customerDetails.reschedule_reason,
        period: this.customerDetails.reschedule_recruiter_dates[0] + "  to  " + this.customerDetails.reschedule_recruiter_dates[1]
      })
    }
  }
setUserForm(){
    if(this.editMode){
      console.log("in editmode");
      this.userDetailsForm.patchValue({
        title:this.customerDetails.job_id[0].job_title,
        name:this.customerDetails.candidates.name,
        location:"",
        mode : "",
        date :"",
      })
    }else{
      console.log("editMode is off");
    }
  }
getEventsUser(id){
    this.appService.getUserEvent(JSON.parse(id)).subscribe((data)=>{
      console.log(data);
      if(data.responseCode === 200){
      this.eventArray = data.result;
       setTimeout(() => {
              this.postEventsOnCalender();
          }, 500);
       console.log(data) ;}
       else{
         this.toastr.warning("No InterView Scheduled")
       }
    },(error)=>{
      console.log(error);
    })
  }
  rescheduleRequest(){
    var control = this.userDetailsForm.controls;
    var form = {
       "interview_date": control.date.value,
       "interview_time": control.date.value
    }
    this.appService.rescheduleRequestDone(form, this.candidateID).subscribe((data)=>{
      console.log(data);
      this.toastr.success(data.responseMessage)
      this.editMode = false ;
      this.events =[];
      this.eventArray  = [];
      this.getEventsUser(this.userId);
      this.router.navigate(['/clientHeader/schedule']);
    },(err)=>{
      console.log(err);
    })
  }
  AddEventInClander(col,i){
  this.events.push({
      title: " Candidate "+this.eventArray[i].candidate_name+"'s Interview is scheduled For "+this.eventArray[i].job_id.job_title +" Job ",
      start: this.eventArray[i].interview_date,
      end: addHours(this.eventArray[i].interview_date, 1),
      color:col,
      draggable: false,
      resizable: {
        beforeStart: false,
        afterEnd: false
      }
    });
  }
postEventsOnCalender(){
  console.log("entry filing",this.eventArray);
   for(let i = 0 ; i< this.eventArray.length ; i++){
    console.log(i);
    if(!this.eventArray[i].status){
      let blue = colors.blue
      this.AddEventInClander(blue,i);
    }else{
      let red = colors.red
      this.AddEventInClander(red,i);
    }
   // this.events.push({
   //    title: " Candidate "+this.eventArray[i].candidate_name+"'s Interview is scheduled For "+this.eventArray[i].job_id.job_title +" Job ",
   //    start: this.eventArray[i].interview_date,
   //    end: addHours(this.eventArray[i].interview_date, 1),

   //    color: colors.red,
   //    draggable: false,
   //    resizable: {
   //      beforeStart: false,
   //      afterEnd: false
   //    }
   //  });
    this.refresh.next();
    console.log(this.events);
   }
   this.refresh.next();
  }
}
