import { NgbModal, ModalDismissReasons, NgbActiveModal, NgbModalRef, NgbModalOptions, NgbTabset } from '@ng-bootstrap/ng-bootstrap';
import { JsonPipe } from '@angular/common';
import {Location} from '@angular/common';

@Component({
  selector: 'app-pi-shortlisted',
  templateUrl: './pi-shortlisted.component.html',
  styleUrls: ['./pi-shortlisted.component.css']
})
export class PiShortlistedComponent implements OnInit {
  userId;
  user;
  allCV;
  allCVS;
  userName="";
  location="";
  companyName= "";
  showPagination = false;
  jobTitle = "";
  status = "";
  page = 1;
  recruiter = false;
  shortListedCandidates = [];
  allCandidates =[];
  constructor(	
  	private modalService: NgbModal,
    private appService:AppService,
    private _location: Location,
    private toastr: ToastrService
  	) {
      this.userId = localStorage.getItem('loginSessId');
      
    }
ngOnInit() {
  this.getCandidates(this.userId)
  this.getProfile(this.userId);
  }
  getCandidates(id){
    this.appService.getPiShortlistedCandidates(this.userId).subscribe((data)=>{
      // this.allCandidates = data.result
      this.ManageData(data.result,JSON.parse(this.userId));
    },err =>{
      console.log(err);
    })
  }
  getProfile(id){
    this.appService.recruiterProfile(JSON.parse(id)).subscribe((data)=>{
      
      var resp = data.json();
      this.user=resp.result;
      if(this.user.user_type==="1"){
        this.recruiter = true;
      }
    },(err)=>{
      console.log(err);
    })
  }
  backClicked() {
    this._location.back();
  }
  ManageData(data,id){

     for(let i = 0 ; i < data.length ; i ++){
       
if(data[i].client.length>0){
      // console.log("userId",this.userId)
      if(this.recruiter){
       if(data[i].recruiter[0]._id === id){
         if(data[i].candidates.ey_shortlist === true){
         this.allCandidates.push(data[i]);
         }
       }
      }else{
        if(data[i].client[0]._id === id){
          if(data[i].candidates.ey_shortlist === true){
          this.allCandidates.push(data[i]);
          }
        }
      }
    }
     }
    //   console.log("first loop",data[i])
    //   for(let j = 0 ; j < data[i].candidates.length; j++){
    //     console.log("secound loop",j)
    //     if(data[i].candidates[j].ey_shortlist === true){
    //       console.log("data");
    //       this.shortListedCandidates.push(data[i].candidates[j])
    //     }
    //   }
    // }
   
    // console.log(this.allCandidates);
  }
  setCV(cv){
    console.log(cv);
        window.open(cv, "_blank");
    }
}
