import {Component, OnInit} from '@angular/core';
import {FormGroup, FormBuilder, Validators, FormControl} from '@angular/forms';
import {ActivatedRoute, Router, Params} from '@angular/router';
import {AppService} from '../app.component.service';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-recruiter-header',
  templateUrl: './recruiter-header.component.html',
  styleUrls: ['./recruiter-header.component.css']
})
export class RecruiterHeaderComponent implements OnInit {
  activeHead = 1;
  changePasswordForm: FormGroup;
  changeSubmitted = false;
//toggle
  show = false;
  user;

  public constructor(
    private router: Router,
    private appService: AppService,
    private toastr: ToastrService) {
    this.user = localStorage.getItem('loginSessId');
  }

  ngOnInit() {
    const routerUrl = this.router.url;
    debugger
    this.initChangePassword();
    this.getDetails()
  }
  userD:any;UserActive:any;uName
getDetails(){
  let userId = localStorage.getItem("loginSessId")
  this.appService.recruiterProfile(JSON.parse(userId)).subscribe(
    (objS) => {
      var resp = objS.json();

      this.userD = resp.result;
      console.log("this.userD",this.userD,this.userD.user_name);
      this.uName=this.userD.user_name
      if(this.userD.status == "active"){
        this.UserActive = true;
      }else{
        this.UserActive = false;
      }
    },
    (objE) => {
      console.log(objE);
    }
  );
}

  // console.log(user)
  // if(user){
  //   user = JSON.parse(user)
  //   return user
  // }


  //toggle
  togglefn() {
    this.show = !this.show;
    console.log(this.show);
  }

  onChangePassword() {
    this.changeSubmitted = true;
    var control = this.changePasswordForm.controls;
    if (this.changePasswordForm.valid && control.password.value == control.confirmPassword.value) {
      var form = {
        'oldPassword': control.oldPassword.value,
        'newPassword': control.password.value,
      };
      console.log(form);
      this.appService.changePassword(JSON.parse(this.user), form).subscribe((data) => {
        this.toastr.success('Change Password Successfully');
        console.log(data);
      }, (err) => {
        this.toastr.success('Invalid Password');
        console.log(err);
      });
    }
  }

  initChangePassword() {
    this.changePasswordForm = new FormGroup({
      oldPassword: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      confirmPassword: new FormControl('', Validators.required),
    });
  }

  logout() {
    localStorage.removeItem('loginSessId');
    localStorage.removeItem('token');
    window.location.href = 'https://peopleinfinia.com';
    // this.router.navigate(['/main/home']);

  }
}
