getdata() {
  this.appService.getEditJobsDetails(this.jobId, JSON.parse(this.userId)).subscribe(
    (data) => {
      this.values = data.result;
      if (this.values.doc !== '') {
        this.Jd = true;
        this.JdDoc = this.values.doc;
      }
      if (this.values.sampleCV !== '') {
        this.showSampleCV = true;
        this.sampleCV = this.values.sampleCV;
      }
      this.locationSelected = this.values.Location;
      for (let i = 0 ; i < this.locationSelected.length ; i++) {
        this.ShowLocation.push(this.locationSelected[i]);
      }
      this.selectIndustryEnd = this.values.industry;
      this.selectFunctionalEnd = this.values.functional_area;
      this.UGCoursesSelected = this.values.ug_course;
      this.exp = this.values.experience.split(' - ');
      this.editPostJobForm.patchValue({
        experiancemin: this.exp[0],
        experiancemax: this.exp[1]
      });
      this.setFormValues();
    if (this.values.draft_status) {this.updateButton = false; }
    }, (err) => {
  });
}

handleFileSelect(evt) {
  this.fileError = false;
  this.fileArray.push(evt.target.files[0].name);
  this.fileName = evt.target.files[0].name;
  let ext = this.fileName.substr(this.fileName.lastIndexOf('.') + 1);
  if (ext === 'pdf') {
    const files = evt.target.files;
    const file = files[0];
    this.typeImage = files[0].type;
    if (files && file) {
      const reader = new FileReader();
      reader.onload = this._handleReaderLoaded.bind(this);
      reader.readAsBinaryString(file);
    }
  } else {
    this.fileError = false;
    this.toastr.warning('Invalid File Type! Only pdf allowed');
  }
}
_handleReaderLoaded(readerEvt) {
  var binaryString = readerEvt.target.result;
  this.base64textString = btoa(binaryString);
  if (this.base64textString) {
    this.toastr.success('uploaded successfully');
      this.doc = this.base64textString;
      this.showJDFile = true;
      this.Jd = false;
      this.JdDoc = this._sanitizer.bypassSecurityTrustUrl('data:base64,' + this.base64textString);
    }
}
removeJD() {
    this.doc = '';
    this.showJDFile = false;
    this.fileName = null;
    this.toastr.warning('JD removed');
}

